@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700;900&family=Merriweather:wght@400;700;900&family=Oswald:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-section{
    background-color: #19192a;
    height: 100vh;
    width: 100%;
}

.home-title{
    color: #e6e6e6;
}
.color-purple{
    color: #8a168a;
}
.get-started-btn{
    background-color: #8a168a;
    border: none;
}
.app-download-btn{
    background-color: transparent;
    border: 2px solid #8a168a;
    display: flex;
    width: 10rem;
    justify-content: space-between;
    align-items: center;
}
.app-download-btn img{
    height: 1.3rem;
    width:  1.3rem;
}
.purple-curve{
    height: 20rem;
    width:  20rem;
    background-color: #8a168a;
    border-radius: 100%;
    position: absolute;
    left: -4rem;
    top: 4rem;
    opacity: 0.3;
    z-index: 0;
}

.home-images{
    position: relative;
    /* background-color: red; */
    width: 50%;
    right: -7rem;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    height: 80vh;
}
.img-all{
    background-color: #e6e6e6;
    box-shadow: 0 0 6px rgb(48, 48, 48);
    height: 19rem;
    width:11rem;
    border-radius: 15px;
    position: absolute;
    z-index: 1;
    overflow: hidden;
    /* transform: scale(0.9); */
}

.img-sec{
    /* background: url('../img/img2.png'); */
    /* background-size: cover; */
    /* background-color: transparent; */
    height: 100%;
    width: 100%;
    border-radius: 10px;
}
.img-phone{
    background-color: #e6e6e6;
    position: absolute;
    height: 22rem;
    width: 13rem;
    z-index: 2;
    border: 13px solid #1a1a1d;
    overflow: hidden;
    box-shadow: 0 0 6px #000;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}
.notch{
    background-color: #1a1a1d;
    position: absolute;
    height: 1.2rem;
    width: 6rem;
    top: -8px;
    border-radius: 10px;

}

.img-1{
    right: 0;
    top: -2rem;
}
.img-2{
    right: 0;
    top: 18rem;
}
.img-3{
    right: 12.5rem;
    top: -7rem;
}
.img-4{
    right: 12.5rem;
    top: 13rem;
}

.img-all img{
    height: 100%;
    width: 100%;
}

.img-4 img{
    position: relative;
    top: -4px;
    min-width: 102%;
    min-height: 106%;
}
.img-2 img{
    position: relative;
    left: -22px;
    min-width: 114%;
}
.img-3 img{
    position: relative;
    top: -4px;
    left: -8px;
    min-width: 114%;
    min-height: 101%;
}

.img-1 img{
    position: relative;
    top: -20px;
    min-height: 110%;
    left: -7px;
    min-width: 108%;
}
.img-5 img{
    position: relative;
    min-width: 104%;
    min-height: 107%;
    top: -14px;
}
