@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700;900&family=Merriweather:wght@400;700;900&family=Oswald:wght@600&display=swap');

nav{
    background-color: #19192a;
    height: 5rem;
}
.sign-in-btn{
    background-color: transparent;
    color: #feffff;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #8a168a;
}
.sign-in-btn:hover,.sign-up-btn:hover{
    color: #feffff;
}
.sign-up-btn{
    background-color: #8a168a;
    border: none;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #feffff;
}
.home-btn{
    font-family: 'Merriweather';
    letter-spacing: 2px;
    color: #feffff;
    z-index: 1000;
}

.home-btn:hover{
    color: #feffff;
}
.navbar-toggler-icon{
    /* background-color: purple; */

}
.navbar-toggler-icon div{
    width: 100%;
    height: 4px;
    border-radius: 4px;
    background-color: #feffff;
    margin-bottom: 5px;
}
.navbar-toggler{
    border: none;
    outline: none;
}
.collapsed{
    outline: none;
    border: none;
}

.show{
    height: 100vh;
    background-color: #19192a;
    /* border: 1px solid purple; */
    position: relative;
    top: 1rem;
    z-index: 1000;
}